'use client'

import { useState, useEffect } from 'react'

// Custom field definition type (matches database schema)
export interface CustomFieldDefinition {
  id: string
  subAccountId: string
  entityType: string
  fieldName: string
  fieldLabel: string
  fieldType: 'text' | 'number' | 'date' | 'dropdown' | 'checkbox' | 'textarea' | 'email' | 'phone' | 'url'
  fieldOptions?: Array<{ value: string; label: string }> // For dropdown
  defaultValue?: string
  isRequired?: boolean
  displayOrder: number
  isActive: boolean
  showInList: boolean
  createdAt?: string
  updatedAt?: string
}

interface CustomFieldInputProps {
  field: CustomFieldDefinition
  value: any
  onChange: (fieldName: string, value: any) => void
  disabled?: boolean
  error?: string
}

export default function CustomFieldInput({
  field,
  value,
  onChange,
  disabled = false,
  error,
}: CustomFieldInputProps) {
  const [localValue, setLocalValue] = useState(value || field.defaultValue || '')

  // Update local value when prop changes
  useEffect(() => {
    setLocalValue(value || field.defaultValue || '')
  }, [value, field.defaultValue])

  // Handle input change
  const handleChange = (newValue: any) => {
    setLocalValue(newValue)
    onChange(field.fieldName, newValue)
  }

  // Render different input types
  const renderInput = () => {
    const baseClasses =
      'w-full px-3 sm:px-4 py-2 sm:py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent disabled:opacity-50 disabled:bg-gray-50 text-base'

    switch (field.fieldType) {
      case 'text':
        return (
          <input
            type="text"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          />
        )

      case 'textarea':
        return (
          <textarea
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            disabled={disabled}
            required={field.isRequired}
            rows={3}
            className={`${baseClasses} resize-none`}
          />
        )

      case 'number':
        return (
          <input
            type="number"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          />
        )

      case 'dropdown':
        return (
          <select
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          >
            <option value="">Select {field.fieldLabel}</option>
            {field.fieldOptions?.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        )

      case 'checkbox':
        return (
          <label className="flex items-center gap-3 p-3 border border-gray-300 rounded-lg hover:bg-gray-50 cursor-pointer">
            <input
              type="checkbox"
              checked={localValue === true || localValue === 'true'}
              onChange={(e) => handleChange(e.target.checked)}
              disabled={disabled}
              className="w-5 h-5 text-purple-600 focus:ring-purple-500 border-gray-300 rounded"
            />
            <span className="text-sm text-gray-700">Yes</span>
          </label>
        )

      case 'date':
        return (
          <input
            type="date"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          />
        )

      case 'email':
        return (
          <input
            type="email"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            placeholder="email@example.com"
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          />
        )

      case 'phone':
        return (
          <input
            type="tel"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            placeholder="+1 (555) 123-4567"
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          />
        )

      case 'url':
        return (
          <input
            type="url"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            placeholder="https://example.com"
            disabled={disabled}
            required={field.isRequired}
            className={baseClasses}
          />
        )

      default:
        return (
          <input
            type="text"
            value={localValue}
            onChange={(e) => handleChange(e.target.value)}
            disabled={disabled}
            className={baseClasses}
          />
        )
    }
  }

  return (
    <div className="space-y-1">
      {/* Label */}
      <label className="block text-sm font-medium text-gray-700">
        {field.fieldLabel}
        {field.isRequired && <span className="text-red-500 ml-1">*</span>}
      </label>

      {/* Input */}
      {renderInput()}

      {/* Error Message */}
      {error && <p className="text-xs text-red-500">{error}</p>}
    </div>
  )
}
