interface InternationalPhoneInputProps {
  value: string
  onChange: (value: string) => void
  required?: boolean
  disabled?: boolean
  className?: string
}

export default function InternationalPhoneInput({
  value,
  onChange,
  required = false,
  disabled = false,
  className = '',
}: InternationalPhoneInputProps) {
  const [selectedCountry, setSelectedCountry] = useState(COUNTRIES[0]) // Default US
  const [phoneNumber, setPhoneNumber] = useState('')
  const [dropdownOpen, setDropdownOpen] = useState(false)
  const [searchTerm, setSearchTerm] = useState('')

  // Parse existing value on mount or when value changes
  useEffect(() => {
    if (value) {
      // Parse the full international phone number
      const country = COUNTRIES.find((c) => value.startsWith(c.dialCode))
      if (country) {
        setSelectedCountry(country)
        setPhoneNumber(value.replace(country.dialCode, '').trim())
      } else {
        // If no match, assume it's just the number without country code
        setPhoneNumber(value)
      }
    }
  }, [value])

  // Format phone number based on country format
  const formatPhoneNumber = (input: string, format: string) => {
    // Remove all non-digit characters
    const digits = input.replace(/\D/g, '')
    
    let formatted = ''
    let digitIndex = 0

    for (let i = 0; i < format.length && digitIndex < digits.length; i++) {
      if (format[i] === '#') {
        formatted += digits[digitIndex]
        digitIndex++
      } else {
        formatted += format[i]
      }
    }

    return formatted
  }

  // Handle phone number input
  const handlePhoneChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const input = e.target.value
    const formattedNumber = formatPhoneNumber(input, selectedCountry.format)
    setPhoneNumber(formattedNumber)
    
    // Combine country code + formatted number
    const fullNumber = `${selectedCountry.dialCode} ${formattedNumber}`.trim()
    onChange(fullNumber)
  }

  // Handle country selection
  const handleCountrySelect = (country: typeof COUNTRIES[0]) => {
    setSelectedCountry(country)
    setDropdownOpen(false)
    setSearchTerm('')
    
    // Re-format existing number with new country format
    if (phoneNumber) {
      const formattedNumber = formatPhoneNumber(phoneNumber, country.format)
      setPhoneNumber(formattedNumber)
      const fullNumber = `${country.dialCode} ${formattedNumber}`.trim()
      onChange(fullNumber)
    }
  }

  // Filter countries based on search
  const filteredCountries = searchTerm
    ? COUNTRIES.filter(
        (c) =>
          c.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
          c.code.toLowerCase().includes(searchTerm.toLowerCase()) ||
          c.dialCode.includes(searchTerm)
      )
    : COUNTRIES

  return (
    <div className={`relative ${className}`}>
      <div className="flex gap-2">
        {/* Country Selector */}
        <div className="relative">
          <button
            type="button"
            onClick={() => setDropdownOpen(!dropdownOpen)}
            disabled={disabled}
            className="flex items-center gap-2 px-3 sm:px-4 py-2 sm:py-3 border border-gray-300 rounded-lg bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent disabled:opacity-50 disabled:cursor-not-allowed transition-all min-w-[120px] sm:min-w-[140px]"
          >
            <span className="text-2xl">{selectedCountry.flag}</span>
            <span className="text-sm font-medium text-gray-700">
              {selectedCountry.dialCode}
            </span>
            <svg
              className={`w-4 h-4 text-gray-500 transition-transform ${
                dropdownOpen ? 'rotate-180' : ''
              }`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M19 9l-7 7-7-7"
              />
            </svg>
          </button>

          {/* Dropdown Menu */}
          {dropdownOpen && (
            <div className="absolute z-50 mt-2 w-80 bg-white border border-gray-200 rounded-lg shadow-xl max-h-96 overflow-y-auto">
              {/* Search Box */}
              <div className="sticky top-0 bg-white border-b border-gray-200 p-3">
                <input
                  type="text"
                  placeholder="Search country..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 text-sm"
                />
              </div>

              {/* Country List */}
              <div className="py-2">
                {filteredCountries.map((country) => (
                  <button
                    key={country.code}
                    type="button"
                    onClick={() => handleCountrySelect(country)}
                    className={`w-full px-4 py-3 flex items-center gap-3 hover:bg-gray-50 transition-colors ${
                      selectedCountry.code === country.code
                        ? 'bg-purple-50 text-purple-700'
                        : 'text-gray-700'
                    }`}
                  >
                    <span className="text-2xl">{country.flag}</span>
                    <div className="flex-1 text-left">
                      <div className="font-medium text-sm">{country.name}</div>
                      <div className="text-xs text-gray-500">{country.code}</div>
                    </div>
                    <span className="text-sm font-medium text-gray-600">
                      {country.dialCode}
                    </span>
                  </button>
                ))}
                
                {filteredCountries.length === 0 && (
                  <div className="px-4 py-8 text-center text-gray-500 text-sm">
                    No countries found
                  </div>
                )}
              </div>
            </div>
          )}
        </div>

        {/* Phone Number Input */}
        <input
          type="tel"
          value={phoneNumber}
          onChange={handlePhoneChange}
          required={required}
          disabled={disabled}
          placeholder={selectedCountry.format.replace(/#/g, '0')}
          className="flex-1 px-3 sm:px-4 py-2 sm:py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent disabled:opacity-50 disabled:bg-gray-50 text-base"
        />
      </div>

      {/* Format Helper Text */}
      <div className="mt-1 text-xs text-gray-500">
        Format: {selectedCountry.dialCode} {selectedCountry.format}
      </div>
    </div>
  )
}
