'use client'

import { useState, useEffect } from 'react'
import CustomFieldInput, { CustomFieldDefinition } from './CustomFieldInput'

interface CustomFieldsSectionProps {
  subAccountId: string
  entityType: 'contact' | 'opportunity' | 'deal' | 'company'
  values: Record<string, any>
  onChange: (values: Record<string, any>) => void
  disabled?: boolean
}

export default function CustomFieldsSection({
  subAccountId,
  entityType,
  values,
  onChange,
  disabled = false,
}: CustomFieldsSectionProps) {
  const [customFields, setCustomFields] = useState<CustomFieldDefinition[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

  // Fetch custom field definitions
  useEffect(() => {
    const fetchCustomFields = async () => {
      try {
        setLoading(true)
        const response = await fetch(
          `/api/v1/custom-fields?subAccountId=${subAccountId}&entityType=${entityType}`
        )
        const data = await response.json()

        if (data.success) {
          // All active fields should show in forms
          setCustomFields(data.data || [])
        } else {
          setError(data.error || 'Failed to load custom fields')
        }
      } catch (err: any) {
        console.error('Failed to fetch custom fields:', err)
        setError('Failed to load custom fields')
      } finally {
        setLoading(false)
      }
    }

    if (subAccountId && entityType) {
      fetchCustomFields()
    }
  }, [subAccountId, entityType])

  // Handle field value change
  const handleFieldChange = (fieldName: string, value: any) => {
    const newValues = {
      ...values,
      [fieldName]: value,
    }
    onChange(newValues)
  }

  // Don't render anything if no custom fields
  if (loading) {
    return (
      <div className="flex items-center justify-center py-8">
        <svg
          className="animate-spin h-8 w-8 text-purple-600"
          fill="none"
          viewBox="0 0 24 24"
        >
          <circle
            className="opacity-25"
            cx="12"
            cy="12"
            r="10"
            stroke="currentColor"
            strokeWidth="4"
          />
          <path
            className="opacity-75"
            fill="currentColor"
            d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
          />
        </svg>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-lg">
        {error}
      </div>
    )
  }

  if (customFields.length === 0) {
    return null // Don't show section if no custom fields
  }

  return (
    <div>
      <h3 className="text-lg font-semibold text-gray-900 mb-3 sm:mb-4">
        Additional Information
      </h3>
      <div className="space-y-3 sm:space-y-4">
        {customFields.map((field) => (
          <CustomFieldInput
            key={field.id}
            field={field}
            value={values[field.fieldName]}
            onChange={handleFieldChange}
            disabled={disabled}
          />
        ))}
      </div>
    </div>
  )
}
